.rotating-rect {
  animation: rotatingRect 10s cubic-bezier(.7,-0.55,.4,1.65) infinite;
}

.rotating-circle {
  animation: rotatingCircle 10s cubic-bezier(.7,-0.55,.4,1.65) infinite;
}

@keyframes rotatingRect {
  from {
    stroke-dashoffset: 0;
  }

  30%, 50% {
    stroke-dashoffset: -400;
  }

  80%, to {
    stroke-dashoffset: -800;
  }
}

@keyframes rotatingCircle {
  from {
    stroke-dashoffset: 314.159;
  }

  30%, 50% {
    stroke-dashoffset: 0;
  }

  80%, to {
    stroke-dashoffset: -314.159;
  }
}



